// 转盘
.turnplate {
  position: absolute;
  bottom: 40px;
  right: 40px;
  background: url(../../assets/image/rtc/turnplate-0.svg) no-repeat;
  background-size: 278px 278px;
  background-position: 11px 11px;
  background-repeat: no-repeat;
  width: 300px;
  height: 300px;
  border-radius: 300px;
  overflow: hidden;
  color: var(--ylr-color-white);
  display: flex;
  flex-wrap: wrap;
  &:after {
    content: '';
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    background: url(../../assets/image/rtc/turnplate.svg) no-repeat;
    background-size: contain;
    width: 300px;
    height: 300px;
    border-radius: 300px;
    animation: rotate 4000ms linear infinite;
    @keyframes rotate {
      0% { transform: rotate(0deg); }
      100% { transform: rotate(360deg); }
    }
  }
  > div {
    cursor: pointer;
    width: 100px;
    height: 100px;
    position: absolute;
    z-index: 1;
    > div {
      text-shadow: 0px 2px 8px #0055A1;
      // background: linear-gradient(180deg, #FFFFFF 0%, #C3E6FF 100%);
      // -webkit-background-clip: text;
      // -webkit-text-fill-color: transparent;
      flex: 1;
      width: 100%;
      height: 100%;
    }
    em {
      content: '';
      display: block;
      margin: 34px auto -4px;
    }
  }
  .btnCircle0 {
    position: absolute;
    width: 75px;
    height: 75px;
    top: 113px;
    left: 113px;
    em {
      margin: 16px auto -4px;
    }
  }
  .btnCircle1 {
    top: -8px;
    left: 97px;
  }
  .btnCircle2 {
    top: 63px;
    left: 195px;
  }
  .btnCircle3 {
    top: 167px;
    left: 156px;
  }
  .btnCircle4 {
    top: 173px;
    left: 42px;
  }
  .btnCircle5 {
    top: 62px;
    left: 5px;
  }
}
