// 来电
.turnplateRingContainer {
  color: var(--ylr-color-white);
  border-radius: 50%;
  text-align: center;
  .defaultCallPhone {
    cursor: default;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    font-family: DINAlternate-Bold, DINAlternate, sans-serif;
    font-weight: bold;
    color: var(--ylr-color-white);
    line-height: 46px;
    margin: -7px 40px 36px;
    overflow: hidden;
    > span {
      max-width: 100%;
      display: flex;
      justify-content: flex-end;
    }
    > em {
      position: absolute;
      display: block;
      margin: 52px 0 0 0;
      font-weight: 400;
      font-size: var(--ylr-font-size);
      font-style: normal;
      opacity: 0.7;
    }
  }
  > div {
    display: inline-block;
    cursor: pointer;
    > i {
      width: 40px;
      height: 40px;
      border-radius: 40px;
      display: block;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: contain;
      margin-bottom: 12px;
    }
    &.acceptRingBtn {
      margin-left: 64px;
      > i {
        box-shadow: 0 0 20px rgb(103 255 93);
        transition: box-shadow 1s;
        animation: shake 800ms ease-in-out;
        &.isVideo {
          box-shadow: 0 0 20px rgb(133, 159, 255);
        }
        &.isNormal {
         box-shadow: none;
         animation: none;
        }
        @keyframes shake {
          10%, 90% { transform: translate3d(-1px, 0, 0); }
          20%, 80% { transform: translate3d(+2px, 0, 0); }
          30%, 70% { transform: translate3d(-4px, 0, 0); }
          40%, 60% { transform: translate3d(+4px, 0, 0); }
          50% { transform: translate3d(-4px, 0, 0); }
        }
      }
    }
    // &.rejectRingBtn {
    //   > i {
    //     background-image: var(--ylr-ico-call-btn-hangup);
    //   }
    // }
  }
}

// 会议成员列表
.member {
  display: flex;
  height: calc(100% - 10px);
  min-height: 514px;
  &.isCall {
    height: auto;
    min-height: auto;
    // margin-top: -3px;
  }
  .video {
    flex: 1;
    position: relative;
    text-align: center;
    > span {
      display: inline-block;
      text-align: center;
      color: var(--ylr-color-white);
      width: 100px;
      margin: 0 -9px 0;
      // padding: 0 20px;
      font-size: calc(var(--ylr-font-size) - 2px);
      cursor: pointer;
      position: relative;
      top: 7px;
      em {
        display: block;
        margin: 0 auto 4px;
      }
    }
    .iframe {
      line-height: 0;
      height: calc(100% - 51px);
      position: relative;
      box-sizing: content-box;
      :global {
        .ant-row {
          margin: 0!important;
          height: 100%;
          background: #000;
        }
        .ant-avatar {
          font-size: 29px!important;
          z-index: 9;
          top: calc(50% - 40px);
          left: calc(50% - 40px);
          position: absolute;
          background: linear-gradient(to bottom, var(--ylr-color-bg-avatar-start) 0%, var(--ylr-color-bg-avatar-end) 100%);
          background-size: 80px 80px;
          background-position: 0 -1px;
        }
        .ant-col {
          // margin: 0 auto;
          border: 2px solid var(--ylr-color-border);
        }
      }
      .name {
        position: absolute;
        background: rgba(142, 142, 142, 0.6);
        color: #fff;
        padding: 0 10px;
        bottom: 5px;
        left: 5px;
        z-index: 9;
        line-height: 30px;
        font-weight: bold;
        em {
          width: 14px;
          height: 14px;
          margin: 0 4px 0 0;
          background-size: contain!important;
          cursor: pointer;
          display: inline-block;
          vertical-align: middle;
          i {
            display: inline-block;
            width: 14px;
            height: 14px;
            margin: 0 0 5px;
          }
        }
      }
      video {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        background: #000;
      }
      .loading {
        position: absolute;
        top: 4px;
        left: 4px;
        right: 4px;
        bottom: 4px;
        background: var(--ylr-color-bg-content);
        display: flex;
        align-items: center;
        justify-content: center;
      }
      iframe {
        border: 0;
        width: 100%;
        height: 100%;
        min-height: 454px;
        margin: 0;
        visibility: hidden;
        position: relative;
        z-index: 9;
      }
    }
    .right {
      position: absolute;
      bottom: 0;
      right: 0;
    }
    .left {
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
  .list {
    width: 200px;
    background: var(--ylr-color-bg-content);
    color: var(--ylr-color-white);
    border-radius: 3px;
    float: right;
    margin: 0 0 0 8px;
    display: flex;
    flex-direction: column;
    :last-child {
      overflow: auto;
    }
    .title {
      font-size: var(--ylr-font-size);
      border-radius: 3px 3px 0 0;
      background: var(--ylr-color-bg-title);
      // height: 23px;
      // line-height: 23px;
      padding: 3px 10px;
      span {
        font-weight: bold;
      }
    }
    .footer {
      background: var(--ylr-color-bg-footer);
      // height: 28px;
      // line-height: 28px;
      border-radius: 0 0 3px 3px;
      padding: 6px 0;
      text-align: center;
      font-size: calc(var(--ylr-font-size) - 2px);
      :global {
        .ant-divider{border-color:rgba(255,255,255,0.4)!important;}
      }
      > span {
        cursor: pointer;
        &:before {
          content: '';
          width: var(--ylr-font-size);
          height: calc(var(--ylr-font-size) + 1px);
          display: inline-block;
          vertical-align: middle;
          margin: -2px 4px 0 0;
          background: url(../../assets/image/rtc/call-btn-invite.svg) no-repeat 50% 50%;
          background-size: contain;
        }
      }
      .btnInvite {
        &::before {
          
        }
      }
      .btnMute {
        &::before {
          margin-top: -4px;
          width: calc(var(--ylr-font-size) - 3px);
          vertical-align: middle;
          background-image: url(../../assets/image/rtc/call-btn-mute.svg);
        }
      }
    }
    :global {
      .ant-empty-description {
        color: var(--ylr-color-white);
      }
      .ant-list {
        flex: 1;
      }
    }
    li {
      justify-content: flex-start;
      color: var(--ylr-color-white);
      border: none;
      padding: 0;
      margin: 8px 16px 0;
      overflow: hidden!important;
      span {
        cursor: pointer;
        max-width: 68px;
        overflow:hidden;
        text-overflow:ellipsis;
        white-space:nowrap;
      }
      span:hover {
        color: #1890ff;
      }
      em {
        width: 14px;
        height: 14px;
        margin: -3px 4px 0;
        cursor: pointer;
        overflow: visible!important;
        i {
          background-size: contain!important;
          display: inline-block;
          width: 14px;
          height: 14px;
        }
      }
    }
  }
  .btnVideo {
    i {
      background: url(../../assets/image/rtc/call-btn-video-open-small.svg) no-repeat;
      background-repeat: no-repeat;
    }
    &.active {
      i {
        background: url(../../assets/image/rtc/call-btn-video-close-small.svg) no-repeat;
        background-repeat: no-repeat;
      }
    }
  }
  .btnAudio {
    i {
      width: 11px;
      margin: 0 0 0 2px;
      background: url(../../assets/image/rtc/call-btn-unmute-small.svg) no-repeat;
      background-repeat: no-repeat;
    }
    &.active {
      i {
        width: 14px;
        margin: 0;
        background: url(../../assets/image/rtc/call-btn-mute-small.svg) no-repeat;
        background-repeat: no-repeat;
      }
    }
  }
  .btnHangup {
    i {
      background: url(../../assets/image/rtc/call-btn-hangup-small.svg) no-repeat;
      background-repeat: no-repeat;
    }
  }
  .btnCall {
    i {
      background: url(../../assets/image/rtc/call-btn-call-small.svg) no-repeat;
      background-repeat: no-repeat;
    }
  }

  // 浅色主题
  &.commonThemeWhite {
    .video {
      > span {
        color: #4C84FF;
      }
      .iframe {
        :global {
          .ant-col {
            border-color: #fff;
          }
        }
      }
    }
    .list {
      background: rgba(211,217,226,0.15);
      border-radius: 8px;
      border: 1px solid #D3D9E2;
      .title {
        background: none;
      }
      .footer {
        background: none;
        border-top: 1px solid #D3D9E2;
        color: #4C84FF;
        > span:before {
          transform: translateY(-32px);
          filter: drop-shadow(0 32px 0 #4C84FF); 
        }
        :global {
          .ant-divider{border-color:#E6EAEF!important;height:20px;}
        }
      }
    }
    .btnAudio,.btnVideo,.btnCall {
      i {
        transform: translateY(-32px);
        filter: drop-shadow(0 32px 0 #4C84FF);
      }
      &.active {
        i {
          transform: unset;
          filter: none;
        }
      }
    }
  }
}
// 最大化或最小化
.max {
  color: var(--ylr-color-primary-border);
  position: absolute;
  right: 30px;
  top: 5px;
  z-index: 1500;
  overflow: hidden;
  font-size: var(--ylr-font-size);
  em {
    display: inline-block;
    width: 32px;
    height: 32px;
    text-align: center;
    margin: -3px 5px 0 8px;
    cursor: pointer;
    :global {
      .ico-min {
        margin-top: 8px;
      }
    }
  }
  // 浅色主题
  &.commonThemeWhite {
    color: var(--ylr-color-white);
  }
}
// 展开收起
.fold {
  position: absolute;
  z-index: 10;
  top: 49%;
  right: var(--ylr-space);
  width: 20px;
  height: 51px;
  line-height: 14px;
  background: var(--ylr-color-bg-title);
  color: var(--ylr-color-white);
  border-radius: 3px 0 0 3px;
  text-align: center;
  font-size: 12px;
  cursor: pointer;
  em {
    display: block;
    margin: 6px auto 4px;
    width: 9px;
    height: 9px;
    background: url(../../assets/image/rtc/call-btn-fold.svg) no-repeat 50% 50%;
    background-size: contain;
    &.isUnfold {
      background-image: url(../../assets/image/rtc/call-btn-unfold.svg);
    }
  }
  // 浅色主题
  &.commonThemeWhite {
    background: rgb(249 250 251);
    border-radius: 2px;
    border: 1px solid #D3D9E2;
    overflow: hidden;
    em {
      transform: translateY(-32px);
      filter: drop-shadow(0 32px 0 #4A5B86); 
    }
  }
}

