      }
    }
    .member .video > span {
      color: var(--ylr-color-link);
      :global {
        .ico-call-invite {
          transform: translateY(9999px);
          filter: drop-shadow(0 -9999px 0 var(--ylr-color-link));
        }
      }
    }
    &.isCallVideo {
      .defaultCallPhone {
        color: #fff;
      }
    }
  }
}

