:global {
    /* 按钮 */
    .commonDiyBtn{min-width:72px;text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.3);padding: 0 7px;line-height: 27px;height: 28px;font-size: 14px;font-family: PingFangSC-Semibold, PingFang SC, sans-serif;font-weight: 600;color: #FFFFFF;line-height: 20px;border-radius: 2px;}
    .commonDiyBtn.ant-btn-default{background: #005684;border: 1px solid #00A6FF;color: #fff;}
    .commonDiyBtn.ant-btn-default:hover{background: #004b74;color: #fff;}
    .commonDiyBtn.ant-btn-primary{background: #00A6FF;border: 1px solid #00F2FF;}
    .commonDiyBtn.ant-btn-primary:hover{background: #019aec;}
    .commonDiyBtn.ant-btn-primary2{background: rgba(0,156,203,0.8);border: 1px solid #00BBF3;}
    .commonDiyBtn.ant-btn-primary2:hover{background: rgba(0, 138, 180, 0.8);}
    .commonDiyBtn.ant-btn-dangerous{background: rgba(255,64,0,0.8);border: 1px solid #FF2800;}
    .commonDiyBtn.ant-btn-dangerous:hover{background: rgba(238, 61, 1, 0.8);border: 1px solid #FF2800;}
    
    /* 页卡 */
    .commonDiyTabs.ant-tabs{color: rgba(255, 255, 255, 0.7);font-size:12px;line-height:31px;padding:0;}
    .commonDiyTabs.ant-tabs .ant-tabs-nav-operations{display: none!important;}
    .commonDiyTabs.ant-tabs .ant-tabs-nav{margin:0;}
    .commonDiyTabs.ant-tabs .ant-tabs-nav::before{border-color: rgba(106, 151, 215, 0.3);bottom:0;}
    .commonDiyTabs.ant-tabs .ant-tabs-tab{color:#fff;padding:0;margin:0 0 0 10px;font-size:12px;width: 68px;display: inline-block;text-align: center;}
    .commonDiyTabs.ant-tabs .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn{color:#fff;}
    .commonDiyTabs.ant-tabs .ant-tabs-tab-btn:active{color:#fff;}
    .commonDiyTabs.ant-tabs .ant-tabs-ink-bar{background:#59AEEA;height:1px!important;}
    .commonDiyTabs.ant-tabs .ant-tabs-nav-wrap::before,.commonDiyTabs.ant-tabs .ant-tabs-nav-wrap::after{display:none;}
    .commonDiyTabs.ant-tabs.ant-tabs-card .ant-tabs-nav .ant-tabs-tab{border-radius:0;border:2px solid #10b6b9;background:none;color:#fff;width:36px;padding: 79px 0 80px 8px;line-height: 22px;font-size: 16px;font-weight: 600;margin:0!important;height: 224px;}
    .commonDiyTabs.ant-tabs.ant-tabs-card .ant-tabs-nav .ant-tabs-tab-active{background:#10b6b9;}
    .commonDiyTabs.ant-tabs.ant-tabs-card .ant-tabs-nav{min-width: auto;}
    .commonDiyTabs.ant-tabs.ant-tabs-card .ant-tabs-content-holder{border: none;}
    .commonDiyTabs.ant-tabs.ant-tabs-card .ant-tabs-tabpane {padding: 0!important;}
    
    /* 表单 */
    .commonDiyForm .ant-form-item-label > label {color: #B2E2FF;}
    .commonDiyForm .ant-input {background: #02234B!important;border: 1px solid rgba(0,200,255,0.6);color: #fff;}
    .commonDiyForm .ant-form-item {margin-bottom: 14px;}
    .commonDiyForm .ant-radio-wrapper {color: #B2E2FF;}
    .commonDiyForm .ant-radio-inner,.commonDiyForm .ant-radio-checked .ant-radio-inner {background: none;border: 1px solid #FFF;}
    
    /* 表格 */
    .commonDiyTable .ant-table {background: none;color: #fff;font-size: 14px;}
    .commonDiyTable .ant-table table tr {border-radius: 0!important;}
    .commonDiyTable .ant-empty-description {color: #fff;}
    .commonDiyTable .ant-table-tbody > tr.ant-table-placeholder:hover > td,.commonDiyTable .ant-table-tbody > tr.ant-table-row:hover > td,.commonDiyTable .ant-table-tbody > tr > td.ant-table-cell-row-hover {background: rgba(255, 255, 255, 0.02);}
    .commonDiyTable .ant-table-thead > tr > th {font-size: 12px;color: #168BDA;background: rgba(16, 98, 159, 0.3);border-color: rgba(16, 98, 159, 0.3);}
    .commonDiyTable .ant-table-tbody > tr > td {font-size: 12px;border-color: rgba(16,98,159,0.3);}
    .commonDiyTable .ant-table-thead > tr > th:not(:last-child):not(.ant-table-selection-column):not(.ant-table-row-expand-icon-cell):not([colspan])::before {display: none;}
    .commonDiyTable .ant-table-tbody > tr.ant-table-row:hover > td, .ant-table-tbody > tr > td.ant-table-cell-row-hover {background: rgb(255 255 255 / 2%);}
    .commonDiyTable .ant-table-row-expand-icon,.commonDiyTable .ant-pagination-item-active {background: none;}
    .commonDiyTable .ant-pagination {margin: 10px 0;}
    .commonDiyTable .ant-table-cell-scrollbar:not([rowspan]) {box-shadow: none;}
    
    /* 树 */
    .commonDiyTree.ant-tree,.commonDiyTree.ant-tree .ant-tree-node-content-wrapper:hover,.commonDiyTree.ant-tree .ant-tree-node-content-wrapper.ant-tree-node-selected {background: none!important;color: #fff;}
    .commonDiyTree.ant-tree .ant-tree-checkbox-inner {background:none;border: 1px solid rgba(39, 125, 237, 0.7)!important;}
    .commonDiyTree.ant-tree .ant-tree-treenode-disabled .ant-tree-node-content-wrapper {color: rgba(255, 255, 255, 0.25);}
    .commonDiyTree.ant-tree .ant-tree-checkbox-checked .ant-tree-checkbox-inner {background:#3E92FF;}
    .commonDiyTree.ant-tree.ant-tree-show-line .ant-tree-indent-unit::before {border-right: 1px dashed rgba(39,125,237,0.4);}
    .commonDiyTree.ant-tree.ant-tree-show-line .ant-tree-switcher {background: none;}
    .commonDiyTree.ant-tree.ant-tree-show-line .ant-tree-switcher-leaf-line {visibility: hidden}
    .commonDiyTree.ant-tree .ant-tree-switcher {position: absolute;right: 0;}
    .commonDiyTree.ant-tree .ant-tree-checkbox {position: relative;left: 3px;}

    /* 发光边框 */
    .commonDiyBorder {
        display: flex;
        background: none!important;padding: 4px;border: 1px solid rgba(0, 136, 255, 0.4);box-shadow: 0 0 6px rgba(0, 136, 255, 0.7) inset;
        &:before,&:after {content:'';width:6px;height:6px;border:1px solid rgb(76, 220, 255);position: absolute;top:-1px;left:-1px;}
        &:before {border-width:1px 0 0 1px;}
        &:after {border-width:1px 1px 0 0;right:-1px;left:auto;}
        > div {
            flex: 1;
            &:before,&:after {content:'';width:6px;height:6px;border:1px solid rgb(76, 220, 255);position: absolute;bottom: -1px;left:-1px;z-index:9;}
            &:before {border-width:0 0 1px 1px;}
            &:after {border-width:0 1px 1px 0;left:auto;right:-1px;}
        }
    }

    /* 地图信息 */
    .commonDiyInfoWindow {
        min-width: 90px;
        padding: 5px 10px;
        box-shadow: 0px 0px 2px #000;
        background: #00aeaa;
        color: #FFF;
        font-size: 12px;
        line-height: 18px;
        border-radius: 4px;
    
        &>div {
          &:first-of-type {
            text-align: left;
            display: flex;
            align-items: center;
            justify-content: space-between;
            img {
              width: 9px;
              height: 9px;
              margin-left: 5px;
              cursor: pointer;
            }
          }
    
          &:last-of-type {
            text-align: right;
          }
        }
    }
    
    /* 对话框 */
    .commonDiyDialog .ant-modal-header {background:none;padding:5px 9px;border:none;}
    .commonDiyDialog .ant-modal-header::before {content:'';display:block;height:2px;background:linear-gradient(to right, rgba(84, 197, 253, 0.43) 0%, rgba(9, 84, 107, 0.25) 100%);position: absolute;top: 33px;left: 10px;right: 10px;}
    .commonDiyDialog .ant-modal-header::after {content:'';display:block;width:4px;height:2px;background:rgb(0, 136, 255);position: relative;top: 5px;z-index: 1;}
    .commonDiyDialog .ant-modal-header .ant-modal-title {font-size: 18px;cursor: default;line-height:23px;font-family: YouSheBiaoTiHei, sans-serif;color: #FFFFFF;}
    .commonDiyDialog .ant-modal-header .ant-modal-title::before {content: '';display: inline-block;background: url(../../assets/image/icon/arrow.svg) no-repeat;background-position:-10px -14px;width:9px;height:9px;}
    .commonDiyDialog .ant-modal-close-x {width:32px;height:32px;}
    .commonDiyDialog .ant-modal-close .anticon-close {vertical-align: 12px;}
    .commonDiyDialog .ant-modal-close .anticon-close svg {display: none;}
    .commonDiyDialog .ant-modal-close .anticon-close::before {content:'';display: inline-block;background: url(../../assets/image/icon/close.svg) no-repeat;width:9px;height:9px;background-size: cover;}
    .commonDiyDialog .ant-modal-content {background:linear-gradient(to bottom, rgba(0, 107, 128, 0.9) 0%,rgba(13, 147, 184, 0.95) 100%);border:1px solid rgba(173, 211, 255, 0.5);border-radius:0;}
    .commonDiyDialog .ant-modal-content::before,.commonDiyDialog .ant-modal-content::after,.commonDiyDialog .ant-modal-body::before,.commonDiyDialog .ant-modal-body::after {content:'';width:6px;height:6px;border:1px solid rgb(76, 228, 255);border-width:0 0 1px 1px;position: absolute;bottom:3px;left:3px;}
    .commonDiyDialog .ant-modal-content::before {border-width:1px 0 0 1px;bottom:auto;top:3px;display: none;}
    .commonDiyDialog .ant-modal-content::after {border-width:1px 1px 0 0;right:3px;left:auto;bottom:auto;top:3px;display: none;}
    .commonDiyDialog .ant-modal-body::after {border-width:0 1px 1px 0;right:3px;left:auto;}
    .commonDiyDialog .ant-modal-body {padding:9px;height: 100%;}
    .commonDiyDialog .ant-modal-footer {border:none;}
    .commonDiyDialog.isSmall .ant-modal-close-x {margin: 3px 4px 0 0;}
    .commonDiyDialog.isSmall .ant-modal-body {padding:0 9px 9px;}
    .commonDiyDialog.isSmall .ant-modal-header .ant-modal-title {line-height: 30px;}
    .commonDiyDialog.isSmall .ant-modal-header::before{height:1px;top: 31px;}
    .commonDiyDialog.isSmall .ant-modal-header::after{top: -4px;height:1px;}
    .commonDiyDialog.isSmall .ant-modal-content::before,.commonDiyDialog.isSmall .ant-modal-content::after{display: block;}
    .commonDiyDialog.isMax {width: 100%!important;top: 0!important;right: 0;bottom: 0;padding: 0;max-width: 100vw!important;}
    .commonDiyDialog.isMax .ant-modal-body {height: calc(100vh - 37px);}
    .commonDiyDialog.isConfirm {z-index: 9999;top: 42%;width: 278px!important;}
    .commonDiyDialog.isConfirm .ant-modal-footer{padding-bottom: 16px;padding-top: 0;}
    .commonDiyDialog.isConfirm .ant-modal-body p {color: #fff;text-align: center;padding: 30px 0 10px;}
    .commonDiyDialogWrap.isNoMark {bottom: auto;right: auto;left: -280px;top: 30%;overflow: visible;}
    .commonDiyDialogWrap.isNoMark.isBottom {bottom: 0;right: 0;left: auto;top: auto;overflow: visible;}
}
